<script>
import axios from 'axios';
import API from '../api'
  export default {
    name: 'Home',
    data() {
      return {
        images: [],
        searchText: '',
        status404: false,
      };
    },
    async created() {
      try{
        this.images = await API.getImages();
      }catch(err) {
        console.log(err)
      }
    },
    methods: {
      async submitForm() {
         try {
            const res = await axios.get(`https://dog.ceo/api/breed/${searchText}/images`);
            this.images = res.data;
        } catch(err) {
            console.log(err)
        }
      }
    }
  }
