<script>
import axios from 'axios';
export default {
    data() {
        return{
           images: [],
           searchText: '',
           notfound: true,
           lit:false,
        };
    },
    methods: {
       async submitForm(searchText) {
             try {
            const res = await axios.get(`https://dog.ceo/api/breed/${this.searchText}/images`);
            this.images = res.data;
            this.notfound = false
            this.lit = true
        } catch(err) {
            console.log(err)
            this.notfound = true
            this.lit = true
        }
       }
    },
}
</script>

