import axios from 'axios';
const entryurl = 'https://dog.ceo/api/breeds/image/random/50';
import { createClient } from 'pexels';
const url = 'https://api.pexels.com/v1/'

const client = createClient('563492ad6f9170000100000161deac9b814940e78be049e3ee138f92');

export default class API {
    // get all random images
    static async getImages() {
        try {
            const res = await axios.get(entryurl);
            return res.data;
        } catch(err) {
            console.log(err)
        }
    }
}
